
export default () =>
<div> 
<div className="wrapper">
  <div className="one"><img src= "./static/austria.jpg" /></div>
  <div className="two">Two</div>
  <div className="three">Three</div>
  <div className="four">Four</div>
  <div className="five">Five</div>
  <div className="six">Six</div>
</div>

<style jsx> {`

	* {box-sizing: border-box;}
}
.wrapper {
  max-width: 940px;
  margin: 0 auto;
  margin-top: 60px;
}

.wrapper > div {
  border: 2px solid rgb(233,171,88);
  border-radius: 5px;
  background-color: rgba(233,171,88,.5);
  padding: 1em;
  color: #d9480f;
}.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}
.one {
  grid-column: 1 / 2;
  grid-row: 1/4;
}
.one img {
	position: relative;
	overflow = hidden;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: block;
        max-width: 100%;
        height: auto;
}
.two { 
  grid-column: 2 / 3;
  grid-row: 1 / 3;
}
.three {
  grid-column: 1;
  grid-row: 4 / 6;
}
.four {
  grid-column: 3;
  grid-row: 3;
}
.five {
  grid-column: 2;
  grid-row: 4;
}
.six {
  grid-column: 3;
  grid-row: 4;
}
`} </style>
</div>



